import { _decorator, Component, EventTouch, Node, PageView } from 'cc';
import { Constants } from '../Constants';
import { GameFacadeInstance } from '../GameFacade';
const { ccclass, property } = _decorator;
const PAGE_COUNT = 4;
@ccclass('HelpView')
export class HelpView extends Component {
    @property(PageView)
    ruleView:PageView = null;

    _pageIndex = 0;
    start() {
 
    }

    update(deltaTime: number) {
        
    }
    onChangeRuleView(event:EventTouch, customEventData:string){
        if(customEventData == 'PAGE_LEFT'){
            this._pageIndex-=1;
            if(this._pageIndex<0) this._pageIndex = PAGE_COUNT -1;
        }
        else{
            this._pageIndex+=1;
            if(this._pageIndex>=PAGE_COUNT) this._pageIndex = 0;
        }
        this.ruleView.setCurrentPageIndex(this._pageIndex);
    }
    onBtnClose(){
        GameFacadeInstance.sendNotification(Constants.GMAENOTIFICATION_ID.CLOSEPOPUPVIEW);
    }
}


