import { _decorator, Component, director, find, Label, Node, ProgressBar, ResolutionPolicy, sys, Vec3, view } from 'cc';
import { globalData } from '../GlobalData';
import { GameFacadeInstance } from '../GameFacade';
import { Constants } from '../Constants';
const { ccclass, property } = _decorator;

@ccclass('LoadScene')
export class LoadScene extends Component {
    @property(ProgressBar)
    gameProgress:ProgressBar = null;
    @property(Label)
    labTip:Label = null;

    _enterGameScene:boolean = false;
    start() {
        GameFacadeInstance.registerMediator(this);
        this.parseUrlData();
        GameFacadeInstance.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_LOGIN);
    }
    protected onLoad(): void {
        if (sys.isMobile) {
            view.setDesignResolutionSize(750,1334,ResolutionPolicy.FIXED_WIDTH);
            let cameraNode = find('Canvas/Camera');
            //90
            cameraNode.eulerAngles = new Vec3(0, 0, 90);
        }
    }
    update(deltaTime: number) {
        
    }
    protected onDestroy(): void {
        GameFacadeInstance.removeMediator(this.name);
    } 
    listNotificationInterests(){
		return [
            {
                evtType:Constants.GMAENOTIFICATION_ID.GAME_ENTERGAMESCENE,
                callback: this.loginFinish
            }
        ];
	}
    loginFinish(){
        if(!this._enterGameScene)
        {
            this._enterGameScene = true;
            this.labTip.string = 'logon in success';
            director.preloadScene('GameScene',this.onLoadingProgress.bind(this),this.onLoadFinished.bind(this));  
        }
    }
    onLoadingProgress(completedCount: number, totalCount: number, item: any){
        let progressValue = completedCount/totalCount;
        this.gameProgress.progress = progressValue;
        this.labTip.string = "Resource Loading "+(progressValue*100).toFixed(0)+"%";
    }
    onLoadFinished(error: null | Error){
        if(error){
            console.log(error.message);
            return;
        }
       director.loadScene('GameScene');
    }
    parseUrlData(){
        console.log('parseUrlData');
        let account = "";
        let token = "";
        var parmStr = window.location.href.split('?')[1];
        if (parmStr) {
            var parms = parmStr.split('&');
            parms.forEach(element => {
                var parm = element.split('=');
                if (parm[0] == "member_account") {
                    account = parm[1];
                }
                else if (parm[0] == "token") {
                    token = parm[1];
                }
            });
        }
        globalData.setUserAccountData(token,account);
    }
}


