import {datatypes} from '../messageDef/GameTableProtocol'
import {Constants} from "../Constants"
import { globalData } from '../GlobalData';
import {director} from 'cc';
const global = globalThis;
    
export let gameTableProxy = global.puremvc.define(
	{
		parent: global.puremvc.Proxy,
		constructor: function () {
			global.puremvc.Proxy.call(this, this.constructor.NAME);
		}
	},
	{
		onRegister: function () {

		},

		registerNetInterface: function (app:any) {
			this.app = app;
			let net = app.net;
			this.net = net;
            this.bSendOption = false;
            //
            net.registerServer(this, 'onSocketLogonSuccess', datatypes.CMD_GR_LogonSuccess);
			net.registerServer(this, 'onSocketLogonFinish', datatypes.CMD_GR_LogonFinish);
			net.registerServer(this, 'onSocketLogonFail', datatypes.CMD_GR_LogonFailure);
            net.registerServer(this, 'onSocketConfigEvent', datatypes.CMD_GR_ConfigServer);
            net.registerServer(this, 'onSocketUserEnter', datatypes.CMD_GR_UserEnter); 
            net.registerServer(this, 'onSocketUserScore', datatypes.CMD_GR_UserScore);
            net.registerServer(this, 'onSocketUserStatus', datatypes.CMD_GR_UserStatus);
            net.registerServer(this, 'onEventGameScene', datatypes.CMD_S_GameScene);
			net.registerServer(this, 'detectSocket', datatypes.CMD_KN_DetectSocket);
            net.registerServer(this, 'onMBLogonSuccess', datatypes.CMD_MB_LogonSuccess);
            net.registerServer(this, 'onMBLogonFailure', datatypes.CMD_MB_LogonFailure);
            net.registerServer(this, 'onSocketRespondError', datatypes.CMD_GA_RespondError);
            net.registerServer(this, 'onSocketRespondRoomClose', datatypes.CMD_GA_RespondRoomClose);
            net.registerServer(this, 'onSocketConfigFinish', datatypes.CMD_GR_ConfigFinish);           
            net.registerServer(this, 'onSocketGameStatus', datatypes.CMD_GF_GameStatus);
            net.registerServer(this, 'onSocketRequestFailure', datatypes.CMD_GR_RequestFailure);
            net.registerServer(this, 'onSocketNoticeReady', datatypes.CMD_GF_NoticeReady);
            net.registerServer(this, 'onSocketTableStatus', datatypes.CMD_GR_TableStatus);
            net.registerServer(this, 'onSocketTableInfo', datatypes.CMD_GR_TableInfo);           
            //
            net.registerClient(this, 'sendLogonRoomData', datatypes.CMD_GR_LogonMobile);
			net.registerClient(this, 'sendDetectSocket', datatypes.CMD_KN_DetectSocket);
            net.registerClient(this, 'sendPhoneNumLogin', datatypes.CMD_MB_LogonByPhoneNum);           
            net.registerClient(this, 'sendUserStandUp', datatypes.CMD_GR_UserStandUp);	
            net.registerClient(this, 'sendGameOption', datatypes.CMD_GF_GameOption);
            net.registerClient(this, 'sendUserReady', datatypes.CMD_GF_UserReady);	
		},
        //
        onSocketLogonSuccess:function(dwUserID:number,dwUserRight:number,dwMasterRight:number){
            globalData.setUserID(dwUserID);
        },
        onSocketLogonFinish:function(dwUserID:number,wKindID:number,wServerID:number){
            console.log('onSocketLogonFinish',dwUserID,wKindID,wServerID);
            if(globalData.wTableID != Constants.INVALID_TABLE)
            {
                this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_ENTERGAMESCENE);             
            }            
        },
        onSocketLogonFail:function(lErrorCode:number,wKindID:number,wServerID:number,szDescribeString:string){
            console.log('onSocketLogonFail',lErrorCode,wKindID,wServerID,szDescribeString);
        },
        onSocketConfigEvent:function(data:any){
            console.log('onSocketConfigEvent');
        },
        onSocketConfigFinish:function(data:any){
            console.log('onSocketConfigFinish');
        },
        onSocketUserEnter:function(data:any){
            console.log('onSocketUserEnter',data,globalData.wUserID);
            let isMyself = false;
            if(globalData.wUserID>0&&globalData.wUserID == data.dwUserID) isMyself = true;          
            if(!globalData.wRoomUserList[globalData.wUserID]){
                if(!isMyself) {
                    console.log("never have own information, do not process other user information");
                    return;
                }
            }
            else{
                if(isMyself){
                    console.log("you already have your own information, do not process your own information again");
                    return;
                }
            }
            //
            let isNeedAdd = true;
            let userItem = globalData.wRoomUserList[data.dwUserID];
            if(userItem){
                //           
                if(userItem.wTableID != Constants.INVALID_TABLE){
                    if((userItem.wTableID!=data.wTableID)||(userItem.wChairID!=data.wChairID)){
                        globalData.onUpDataTableUser(userItem.wTableID,userItem.wChairID,null);
                    }
                }
                isNeedAdd = false;
                //
                userItem.cbUserStatus = data.cbUserStatus;
                userItem.wTableID = data.wTableID;
                userItem.wChairID = data.wChairID;
                userItem.szNickName = data.szNickName;
                userItem.lScore = data.lScore;
            }
            if(isNeedAdd){
                globalData.wRoomUserList[data.dwUserID] = data;
            }
            //
            if(isMyself){
                globalData.wTableID = data.wTableID; 
                globalData.wChairID = data.wChairID;
                console.log(""+globalData.wTableID+";"+globalData.wChairID);
                globalData.wUserScore = data.lScore;
                globalData.wFaceID = data.wFaceID;
                globalData.wSzNickName = data.szNickName;
            }
            if(data.wTableID != Constants.INVALID_TABLE && data.cbUserStatus != Constants.USER_STATUS.US_LOOKON){
                globalData.onUpDataTableUser(data.wTableID,data.wChairID,data);
            }


        },
        onSocketUserScore:function(data:any){
            console.log('onSocketUserScore',data);
            let userItem = globalData.wRoomUserList[data.dwUserID];
            if(userItem){
                //
                userItem.lScore = data.lScore;
                userItem.lDiamond = data.lDiamond;
                userItem.lExperience = data.lExperience;
                userItem.lLpValues = data.lLpValues;
                userItem.lTicket = data.lTicket;
                //
                if(userItem.dwUserID == globalData.wUserID){
                    globalData.wUserScore = data.lScore;
                }
            }
        },
        onSocketUserStatus:function(dwUserID:number,wTableID:number,wChairID:number,cbUserStatus:number){
            console.log('onSocketUserStatus',dwUserID,wTableID,wChairID,cbUserStatus);
            //
            if(cbUserStatus == Constants.USER_STATUS.US_LOOKON) return;
            if(!globalData.wRoomUserList[globalData.wUserID]) return;
            let roomUserItem = globalData.wRoomUserList[dwUserID];
            //
            if(!roomUserItem){
               return;
            }
            //
            let oldstatus = {
                cbUserStatus:roomUserItem.cbUserStatus,
                wTableID:roomUserItem.wTableID,
                wChairID:roomUserItem.wChairID
            };      
            //
            roomUserItem.cbUserStatus = cbUserStatus;
            roomUserItem.wTableID = wTableID;
            roomUserItem.wChairID = wChairID;
            //
            if(oldstatus.wTableID!=Constants.INVALID_TABLE){
                if((oldstatus.wTableID != wTableID)||(oldstatus.wChairID!=wChairID)){
                    globalData.onUpDataTableUser(oldstatus.wTableID,oldstatus.wChairID,null);
                }
            }
            //
            globalData.onUpDataTableUser(wTableID,wChairID,roomUserItem);
            let isMyself = (globalData.wUserID == dwUserID);
            if(isMyself){
                globalData.wTableID = wTableID;
                globalData.wChairID = wChairID;
                if(cbUserStatus == Constants.USER_STATUS.US_NULL){
                    //
                    this.sendExitTable();  
                }
                else if(cbUserStatus == Constants.USER_STATUS.US_FREE && oldstatus.cbUserStatus>Constants.USER_STATUS.US_FREE){
                    if(!globalData.wChangeDesk) this.sendExitTable();
                }
                else if(cbUserStatus>Constants.USER_STATUS.US_FREE && oldstatus.cbUserStatus < Constants.USER_STATUS.US_SIT){
                    //
                    if(globalData.wChangeDesk){
                        globalData.wChangeDesk = false;
                        this.bSendOption = false;
                    }
                    this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_ENTERGAMESCENE);  
                }
                else if(globalData.wChangeDesk){
                    //
                    //this.sendGameOption();
                }
                else{
                    //

                }

            }
            else{
                //
                if(cbUserStatus == Constants.USER_STATUS.US_NULL || (cbUserStatus == Constants.USER_STATUS.US_FREE)){
                    console.log("",dwUserID);
                    delete globalData.wRoomUserList[dwUserID];
                }
            }
           
        },
        onSocketRequestFailure:function(lErrorCode:number,szDescribeString:string){
            console.log("onSocketRequestFailure",lErrorCode,szDescribeString);
            if(lErrorCode>0&&szDescribeString.length>0){
                this.sendExitTable();
            }
        },
        onEventGameScene:function(data:any){
            console.log('onEventGameScene',data);
            this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_GAMESCENE,data);
        },
        onMBLogonSuccess:function(data:any){
            console.log("onMBLogonSuccess");
            console.log(data);
            globalData.wUserID = data.dwUserID;
            globalData.wSzToken = data.szToken;
            globalData.wSzAccounts = data.szAccounts;
            //this.sendLogonRoomData();
        },
        onMBLogonFailure:function(lResultCode:number,szDescribeString:string){
            console.log("onMBLogonFailure");
            console.log(lResultCode);
            console.log(szDescribeString);
        },
        onSocketRespondError:function(cbShutDown:number,szErrorCode:string){
            console.log("onSocketRespondError",cbShutDown,szErrorCode);
        },
        onSocketRespondRoomClose:function(dwUserID:number,dwServerID:number,dwReason:number,szErrorCode:string){
            console.log("onSocketRespondRoomClose",dwUserID,dwServerID,dwReason,szErrorCode);
        },
        detectSocket: function () {
            console.log("detectSocket");
			this.net.callServer('sendDetectSocket');
		},
        onSocketGameStatus:function(cbGameStatus:number,cbAllowLookon:number){
            console.log("onSocketGameStatus",cbGameStatus,cbAllowLookon);
            globalData.wGameStatus = cbGameStatus;
        },
        onSocketNoticeReady:function(data:any){
            console.log("onSocketNoticeReady");
            this.sendUserReady();
        },
        onSocketTableStatus:function(wTableID:number, cbTableLock:number,cbPlayStatus:number,lCellScore:number){
            console.log("onSocketTableStatus");
        },
        onSocketTableInfo:function(data:any){
            console.log("onSocketTableInfo");
        },
        //
        sendLogonRoomData:function(){
            let app = this.app;
            app.connect(function () {             
                app.net.callServer('sendLogonRoomData', Constants.GAME_ID,2,1,2,Constants.SERVER_ID,globalData.wUserID,globalData.wSzToken,"abcdefghijklmn",globalData.wSzAccounts);    
            });   
        },     
        sendPhoneNumLogin:function(phoneNum:string){
            let app = this.app;
            app.connect(function () {            
				app.net.callServer('sendPhoneNumLogin', 1,1,"default",phoneNum,"1111",1,"","","","","");               
            });
        },
        sendExitTable:function():string{
            let roomUserItem = globalData.wRoomUserList[globalData.wUserID];
            if(!roomUserItem) return "";
            if(roomUserItem.cbUserStatus>=Constants.USER_STATUS.US_FREE){
                this.sendStandUpData(1);
            }
            else{
                return "";
            }
            let curScene = director.getScene();
            if(curScene &&curScene.name == "GameScene"){
                director.loadScene("LoadScene");
            }
            return "";
        },
        sendStandUpData:function(force:number){
            force = (force) ? 1 : 0;
            this.app.net.callServer('sendUserStandUp', globalData.wTableID,globalData.wChairID,force);
        },
        sendGameOption:function(){
            if(this.bSendOption){
                console.log("sendGameOption");
                return
            }
            this.bSendOption = true;
            this.app.net.callServer('sendGameOption', 0,101122049,2);
        },
        sendUserReady:function(){
            this.app.net.callServer('sendUserReady');
        }

	},
	{
		NAME: 'GameTableProxy'
	});