import { _decorator, Component, Node, Label, Button, tween, Animation, Sprite, resources, SpriteAtlas, Tween, UITransform, Prefab, EventTouch, input, Input, instantiate, Vec3, Vec2, find, sys } from 'cc';
const { ccclass, property } = _decorator;
import { FruitRollControl } from './FruitRollControl';
import { Constants } from '../Constants';
import { GameFacadeInstance } from '../GameFacade';
import { globalData } from '../GlobalData';
import { RewardAniNode } from './RewardAniNode';
import { AudioControl } from './AudioControl';
const ROLLNODE_COUNT = 5;
const LinePosConfig = [
    [{x:1, y:2}, {x:2, y:2}, {x:3, y:2}, {x:4, y:2}, {x:5, y:2} ],
    [{x:1, y:3}, {x:2, y:3}, {x:3, y:3}, {x:4, y:3}, {x:5, y:3} ],
    [{x:1, y:1}, {x:2, y:1}, {x:3, y:1}, {x:4, y:1}, {x:5, y:1} ],
    [{x:1, y:3}, {x:2, y:2}, {x:3, y:1}, {x:4, y:2}, {x:5, y:3} ],
    [{x:1, y:1}, {x:2, y:2}, {x:3, y:3}, {x:4, y:2}, {x:5, y:1} ],
    [{x:1, y:3}, {x:2, y:3}, {x:3, y:2}, {x:4, y:1}, {x:5, y:1} ],
    [{x:1, y:1}, {x:2, y:1}, {x:3, y:2}, {x:4, y:3}, {x:5, y:3} ],
    [{x:1, y:2}, {x:2, y:1}, {x:3, y:2}, {x:4, y:3}, {x:5, y:2} ],
    [{x:1, y:2}, {x:2, y:3}, {x:3, y:2}, {x:4, y:1}, {x:5, y:2} ],
];
@ccclass('GameScene')
export class GameScene extends Component {
    @property(Node)
    rollNodes:Node[] = [];
    @property(Node)
    nodeLines:Node[] = [];
    @property(Node)
    nodeUserInfo:Node = null;
    @property(Label)
    labWinScore:Label = null;
    @property(Label)
    labRewardPool:Label = null;
    @property(Node)
    btnSpin:Node = null;
    @property(Node)
    btnFreeSpin:Node = null;
    @property(Node)
    nodeShowGetFreeNum:Node = null;
    @property(Button)
    btnAddBet:Button = null;
    @property(Button)
    btnDecBet:Button = null;
    @property(Label)
    labBetScore:Label = null;
    @property(Node)
    nodeStartRod:Node = null;
    @property(Node)
    rewardAniNode:Node = null;
    @property(Prefab)
    helpView:Prefab = null;
    @property(Prefab)
    setView:Prefab = null;
    @property(AudioControl)
    audioControl:AudioControl = null;
    
    _popUpView:Node = null;
    _lineCount:number[] = [];
    _rollNodeIndex:number = 0;
    _lineWinScore:number[] = [];
    _itemType:number[] = [];
    _itemInfo:number[][] = [];
    _getFreeNum:number = 0;
    _winScore:number = 0;
    _freeNum:number = 0;
    _poolRewardNum:number = 0;
    _poolNum:number = 0;
    _cellScore:number = 0;
    _betScore:number[] = [];
    _betIndex:number = 0;
    _isSpin:boolean = false;
    _maxBetIndex:number = 0;
    _isAuto:boolean = false;
    _isHoldBtn:boolean = false;
    _gameProxy:any = null;
    _gameTableProxy:any = null;
    start() {
        GameFacadeInstance.registerMediator(this);
        this._gameProxy = GameFacadeInstance.retrieveProxy("GameProxy");
        this._gameTableProxy = GameFacadeInstance.retrieveProxy("GameTableProxy");
        let startRodAnimation = this.nodeStartRod.getComponent(Animation);
        if(startRodAnimation){
            startRodAnimation.on(Animation.EventType.FINISHED,function(){
                //
                this.setRollNodesStatus(Constants.ROLL_STATUS.RollNormall_Status);
                //
                this._gameProxy.requestSpin(this._betIndex,(this._freeNum>0));
                //
                if(this._freeNum>0){
                    this._freeNum-=1;
                    this.updateFreeNum();
                }
            },this);
        }
        this.initUserInfo();
        this.btnSpin.on(Node.EventType.TOUCH_START, this.onBtnSpinTouch, this);
        this.btnSpin.on(Node.EventType.TOUCH_END, this.onBtnSpinTouch, this);
        this.btnFreeSpin.on(Node.EventType.TOUCH_START, this.onBtnSpinTouch, this);
        this.btnFreeSpin.on(Node.EventType.TOUCH_END, this.onBtnSpinTouch, this);
        input.on(Input.EventType.TOUCH_END,this.onTouchEnd,this);
        this._gameTableProxy.sendGameOption();
    }
    protected onLoad(): void {
        if (sys.isMobile) {
            let cameraNode = find('Canvas/Camera');
            //90
            cameraNode.eulerAngles = new Vec3(0, 0, 90);
        }
    }
    update(deltaTime: number) {

    }
    onDestroy(){
        GameFacadeInstance.removeMediator(this.name);
        Tween.stopAll();
        input.off(Input.EventType.TOUCH_END,this.onTouchEnd,this);
    }
    listNotificationInterests(){
        return[{
                evtType:Constants.GMAENOTIFICATION_ID.FRUITROLLFINISH,
                callback: this.rollFinishCallBack
		    },
            {
                evtType:Constants.GMAENOTIFICATION_ID.CLOSEPOPUPVIEW,
                callback: this.closePopUpView
            },
            {
                evtType:Constants.GMAENOTIFICATION_ID.GAME_GAMESCENE,
                callback: this.onGameScene
            },
            {
                evtType:Constants.GMAENOTIFICATION_ID.GAME_GAMESTART,
                callback: this.onGameStart
            },
            {
                evtType:Constants.GMAENOTIFICATION_ID.GAME_GAMECONCLUDE,
                callback: this.onGameConclude
            },
            {
                evtType:Constants.GMAENOTIFICATION_ID.GAME_SENDREWARDPOOL,
                callback: this.onSendRewardPool
            },
        ]
    }
    onTouchEnd(event: EventTouch){
        if(this._popUpView){
            let popViewRect = this._popUpView.getComponent(UITransform).getBoundingBox();
            let touchPos = event.getUILocation();
            let nodePos = this.node.getComponent(UITransform).convertToNodeSpaceAR(new Vec3(touchPos.x,touchPos.y,0));          
            if(!popViewRect.contains(new Vec2(nodePos.x,nodePos.y))){
                this._popUpView.destroy();
                this._popUpView = null;
            }
        }
    }
    onBtnSpinTouch(event:Event){
        if(this._isSpin && !this._isAuto) return;
        let touchNode = event.target;
        if(event.type == Node.EventType.TOUCH_START){
            this._isHoldBtn = false;
            tween(touchNode)
            .delay(1)
            .call(()=>{
                this._isHoldBtn = true;
            })
            .start();
        }
        else if(event.type == Node.EventType.TOUCH_END){
            Tween.stopAllByTarget(touchNode);
            if(this._isAuto) {
                this._isAuto = false;
                this.updateButtonState();
            }
            else {
                 //
                if(this._isHoldBtn)
                {  
                    this._isAuto = true;
                    this.updateButtonState();
                }
                this.onClickStart();
            }
        }
    }
    openPopUpView(event:EventTouch, customEventData:string){
        if(this._popUpView) return;
        let popUpView:Node = null;
        if(customEventData == 'HelpView'){
            popUpView = instantiate(this.helpView);
        }
        else if(customEventData == 'SetView'){
            popUpView = instantiate(this.setView);
        }
        if(popUpView){
            this.node.addChild(popUpView);
            popUpView.setSiblingIndex(-1);
            this._popUpView = popUpView;
        }
    }
    closePopUpView(){
        if(this._popUpView){
            this._popUpView.destroy();
            this._popUpView = null;
        }
    }
    onGameScene(data:any){
        this._betIndex = 0;
        this._maxBetIndex = data.cbBetCount;
        this._cellScore = data.lCellScore;
        this._betScore = data.dBetScore;

        this.updateRewardPool(data.PoolNum);
        this.updateBetScore();
        // 
        if(globalData.wGameStatus == Constants.GAME_STATUS.GAME_SCENE_PLAY){
            //
            this.setRollNodesStatus(Constants.ROLL_STATUS.RollNormall_Status);

            this.onGameStart(data);
        }
        this._gameTableProxy.sendUserReady();
    }
    onGameStart(data:any){
        this._freeNum = data.nTotalGiveAwayCount;
        this._getFreeNum = data.nGiveAwayCount;
        this._winScore = data.dUserTotalWinScore;
        this._poolRewardNum = data.nPoolRewardScore;
        this._itemInfo = data.cbItemInfo;
        this._itemType = data.cbItemType;
        this._lineWinScore = data.dLineScore;
        this._lineCount = data.cbItemLineCount;
        // 
        console.log("onGameStart:",data);
        if(data.cbFreeSpinState == 0 || data.cbFreeSpinState == 3){
            globalData.wUserScore+=(data.dUserTotalWinScore+data.nPoolRewardScore);
        }
        for(let i = 0;i<Constants.ITEM_X_COUNT;i++){
            this.rollNodes[i].getComponent(FruitRollControl).setResultData(data.cbItemInfo[0][i],data.cbItemInfo[1][i],data.cbItemInfo[2][i]);
        }
        this.setRollNodesStatus(Constants.ROLL_STATUS.SlowDown_Status);
    }
    onGameConclude(){
        //this._gameTableProxy.sendUserReady();
    }
    onSendRewardPool(poolNum:number){
        this.updateRewardPool(poolNum);
    }
    setRollNodesStatus(rollStatus:number){
        if(rollStatus == Constants.ROLL_STATUS.RollNormall_Status){
            for(let i = 0;i<this.rollNodes.length;i++){
                this.rollNodes[i].getComponent(FruitRollControl).setRollStatus(rollStatus);
            }
        }
        else if(rollStatus == Constants.ROLL_STATUS.SlowDown_Status){
            this._rollNodeIndex = 0;
            this.audioControl.playEffect('rollstop');
            this.rollNodes[0].getComponent(FruitRollControl).setRollStatus(rollStatus);
        }
    }
    onClickExit(){
        console.log("onClickExit");
    }
    initUserInfo(){
        let labUserName = this.nodeUserInfo.getChildByName('lab_userName').getComponent(Label);
        if(labUserName) labUserName.string = globalData.wSzNickName;
        let headNode = this.nodeUserInfo.getChildByName('spr_head');
        if(headNode){
            resources.load('avatar',SpriteAtlas,(err, atlas)=>{
                if(err){
                    console.log(err);
                    return;
                }
                let frame = atlas.getSpriteFrame("texture/avatar/avatar_min_"+globalData.wFaceID); 
                if(frame) {
                    headNode.getComponent(Sprite).spriteFrame = frame;
                    let nodeSizeHeight = headNode.getComponent(UITransform).contentSize.height;
                    let showSizeHeight = this.nodeUserInfo.getComponent(UITransform).contentSize.height-30;
                    headNode.setScale(showSizeHeight/nodeSizeHeight,showSizeHeight/nodeSizeHeight);
                }
            });
        }
        this.updateUserScore();
    }
    rollFinishCallBack(){
        this.audioControl.playEffect('rollstop');
        this._rollNodeIndex+=1;
        if(this._rollNodeIndex<ROLLNODE_COUNT) {
            console.log('rollFinishCallBack:SlowDown_Status:'+this._rollNodeIndex);
            this.rollNodes[this._rollNodeIndex]?.getComponent(FruitRollControl).setRollStatus(Constants.ROLL_STATUS.SlowDown_Status);
        }
        else{
            console.log('rollFinishCallBack:requestStop');
            this.requestStop();
            this.showReward();
            this._rollNodeIndex = 0;
        }
    }
    //
    showReward(){
        //
        let winScore = 0;
        //
        let winPos = [];
        for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
            if(this._lineCount[i] && this._lineCount[i]>0 && this.nodeLines[i]){
                //
                winScore+=this._lineWinScore[i]; 
                //
                this.nodeLines[i].active = true;
                //
                if(this._lineCount[i] >= 3){
                    if(this._itemType[i] == 1){
                        //  
                        for(let n = 0;n<Constants.ITEM_X_COUNT;n++){
                            if(LinePosConfig[i][n]){
                                let item_x = LinePosConfig[i][n].x;
                                let item_y = LinePosConfig[i][n].y;
                                if(item_y == 1) item_y = 3;
                                else if(item_y == 3) item_y = 1;
                                if(this._itemInfo[item_y-1][item_x-1] == this._itemType[i]){
                                    winPos.push(LinePosConfig[i][n]);
                                }
                            }
                        }
                    }
                    else{
                        //  
                        for(let n = 0;n<this._lineCount[i];n++){
                            if(LinePosConfig[i][n]){
                                winPos.push(LinePosConfig[i][n]);
                            }
                        }
                    }
                }

            }
        }
        //
        if(winScore>0 || this._getFreeNum>0){
            //
            for(let i = 0;i<Constants.ITEM_X_COUNT;i++){
                this.rollNodes[i].getComponent(FruitRollControl).setShowState(true);
            }
            //
            for(let pos of winPos){
                this.rollNodes[pos.x-1]?.getComponent(FruitRollControl).setRewardItem(pos.y);
            }
        }
        this.updateUserScore();
        this.updateWinScore();
        this.updateFreeNum();
        this.updateFreeState();
        //
        console.log('',this._poolRewardNum,winScore,globalData.wUserScore);
        if(this._poolRewardNum>0 || winScore>0){
            //
            let singleBetNum = this._cellScore*this._betScore[this._betIndex];
            let isBigReward = (winScore/singleBetNum)>=20;
            //
            if(isBigReward) {
               //
               this.audioControl.playEffect('bigreward');
            }
            else {
               //
               this.audioControl.playEffect('normalreward');
            }
            //
            this.rewardAniNode.getComponent(RewardAniNode).showRewardAni(this._poolRewardNum,winScore,isBigReward,this);
        }
        else{
            this.rewardFinish(false);
        }
    }
    rewardFinish(isReward:boolean = true){
        this._isSpin = false;
        this.updateButtonState();
        // 
        if(this._isAuto){
            if(isReward) this.onClickStart();
            else setTimeout(()=>{this.onClickStart();},2);
        } 
    }
    //
    updateUserScore(){
        let labUserScore =  this.nodeUserInfo.getChildByName('lab_userScore').getComponent(Label);
        if(labUserScore) labUserScore.string = Constants.FormatScore(globalData.wUserScore);
    }
    //
    updateWinScore(){
        this.labWinScore.string = Constants.FormatScore(this._winScore);
    }
    //
    updateRewardPool(poolNum:number){
        if(this._poolNum<=0){
            this._poolNum =  poolNum;
            this.labRewardPool.string = Constants.FormatScore(this._poolNum);
        } 
        else{
            let addTimes = 50;
            let addNum = (poolNum - this._poolNum)/addTimes;
            let addNumTween  =  tween(this.labRewardPool)
                                .delay(0.05)
                                .call(()=>{
                                    this._poolNum += addNum;
                                    this.labRewardPool.string = Constants.FormatScore(this._poolNum);
                                });
            tween(this.labRewardPool).repeat(addTimes,addNumTween).start();
        } 
    }
    //
    updateFreeNum(){
        this.btnFreeSpin.getComponentInChildren(Label).string = this._freeNum.toString();
    }
    //
    updateFreeState(){
        if(this._getFreeNum>0){
            // 
            this.showGetFreeNum();
        }
        else{
            this.btnSpin.active = (this._freeNum<=0);
            this.btnFreeSpin.active = (this._freeNum>0);
        }
    }
    //
    updateButtonState(){
        console.log('');
        this.btnAddBet.interactable =  !this._isSpin&&this._freeNum <= 0;
        this.btnDecBet.interactable = !this._isSpin&&this._freeNum <= 0;
        this.nodeStartRod.getComponentInChildren(Button).interactable =  !this._isSpin && !this._isAuto;
        this.btnSpin.getComponent(Button).interactable = !this._isAuto;
        this.btnFreeSpin.getComponent(Button).interactable = !this._isAuto;
    }
    showGetFreeNum(){
        if(this.nodeShowGetFreeNum&&(!this.nodeShowGetFreeNum.active)){
            this.nodeShowGetFreeNum.active = true;
            this.nodeShowGetFreeNum.getComponentInChildren(Label).string = this._getFreeNum.toString();
            tween(this.nodeShowGetFreeNum)
            .delay(1)
            .call(()=>{
                this.nodeShowGetFreeNum.active = false;
                this.btnSpin.active = (this._freeNum<=0);
                this.btnFreeSpin.active = (this._freeNum>0);
            })
            .start();
        }
    }
    //
    onClickBet(event:Event, customEventData:string){
        if(customEventData === 'DecBet'){
            this._betIndex-=1;
            if(this._betIndex<0) this._betIndex = this._maxBetIndex;
        }
        else{
            this._betIndex+=1;
            if(this._betIndex>this._maxBetIndex) this._betIndex = 0;
        }
        //
        this.updateBetScore();
    }
    updateBetScore(){
        let betNum = this.getTotalBet();
        this.labBetScore.string = Constants.FormatScore(betNum);
    }
    getTotalBet():number{
        let betNum = 0;
        console.log('getTotalBet', this._cellScore,this._betScore,this._betIndex);
        if(this._betScore[this._betIndex]){
           betNum = this._cellScore*Constants.MAX_LINE_COUNT*this._betScore[this._betIndex];
        } 
        return betNum;
    }
    onClickStart(){
        if(this._isSpin) return;
        if(this._freeNum<=0){
            let totalBet = this.getTotalBet();
            if(globalData.wUserScore<totalBet){
                console.log(',');
                if(this._isAuto){
                    this._isAuto = false;
                    this.updateButtonState()
                }
                return;
            }
            //
            globalData.wUserScore -=totalBet;
            console.log("onClickStart:",globalData.wUserScore,totalBet);
            this.updateUserScore();
            //
            this._winScore = 0;
            this.updateWinScore();
        }
        this._isSpin = true;
        this.updateButtonState();
        //
        for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
            this.nodeLines[i].active = false;
        }
        //
        this.audioControl.playEffect('start');
        //
        this.nodeStartRod.getComponent(Animation)?.play();
    }
    //
    requestStop(){
        if(this._freeNum>0){
            this._gameTableProxy.sendUserReady();
        }
        else{
            console.log('');
            this._gameProxy.requestGameConclude();
        }
    }
}


