import { _decorator, AudioClip, AudioSource, Component, Node, sys } from 'cc';
import { globalData } from '../GlobalData';
import { GameFacadeInstance } from '../GameFacade';
import { Constants } from '../Constants';
const { ccclass, property } = _decorator;
const AUDIO_CONFIG = {
    'start':0,
    'rollstart':1,
    'rollstop':2,
    'bigreward':3,
    'normalreward':4
};
const MUSIC_KEY = 'SuperFruit_AudioMusic';
const EFFECT_KEY = 'SuperFruit_AudioEffect';
@ccclass('AudioControl')
export class AudioControl extends Component {
    @property(AudioSource)
    audioMusic:AudioSource = null;
    @property(AudioSource)
    audioEffect:AudioSource = null;
    @property(AudioClip)
    audioClips:AudioClip[] = [];
    
    _musicEnable:boolean = true;
    _effectEnable:boolean = true;
    start() {
       let musicEnable = sys.localStorage.getItem(MUSIC_KEY);
       if(musicEnable) this._musicEnable = musicEnable==1?true:false;
       let effectEnable = sys.localStorage.getItem(EFFECT_KEY);
       if(effectEnable) this._effectEnable = effectEnable == 1?true:false;       
       if(this._musicEnable) this.audioMusic.play();
    
       globalData.wEffctEnable = this._effectEnable;
       globalData.wMusicEnable = this._musicEnable;
       GameFacadeInstance.registerMediator(this);
    }

    update(deltaTime: number) {
        
    }
    protected onDestroy(): void {
        GameFacadeInstance.removeMediator(this.name);
    }
    listNotificationInterests(){
        return[{
                evtType:Constants.GMAENOTIFICATION_ID.SETAUDIOENABLE,
                callback: this.setAudioEnable
		    }
        ]
    }
    playEffect(effectName:string){
        if(!this._effectEnable) return;
        let audioClip = this.audioClips[AUDIO_CONFIG[effectName]];
        if(audioClip){
            this.audioEffect.playOneShot(audioClip);
        }
    }
    setAudioEnable(musicEnable:boolean,effectEnable:boolean){
        if(musicEnable!=this._musicEnable){
            this._musicEnable = musicEnable;
            if(this._musicEnable)  this.audioMusic.play();
            else this.audioMusic.stop();
            sys.localStorage.setItem(MUSIC_KEY, this._musicEnable?1:0);
            globalData.wMusicEnable = this._musicEnable;
        }
        if(effectEnable!=this._effectEnable){
            this._effectEnable = effectEnable;
            sys.localStorage.setItem(EFFECT_KEY,this._effectEnable?1:0);
            globalData.wEffctEnable = this._effectEnable;
        }
    } 
}


