datatypes.CMD_GR_UserEnter = {
	'mainCmdID': MDM_GR_USER,
	'subCmdID': SUB_GR_USER_ENTER,
	'argTypes': [
        //
		{
			'name': 'dwGameID',  //ID
		},
		{
			'name': 'dwUserID',   //ID
		},
		{
			'name': 'wFaceID',  //
		},
        {
			'name': 'cbGender',  //
		},
        //
        {
			'name': 'wTableID',  //
		},
		{
			'name': 'wChairID',   //
		},
		{
			'name': 'cbUserStatus',  //
		},
        //
        {
			'name': 'lScore',  //
		},
        {
			'name': 'lDiamond',  //
		},
		{
			'name': 'lExperience',   //
		},
		{
			'name': 'lLpValues',  //LP
		},
        {
			'name': 'lTicket',  //
		},
        //vip
        {
			'name': 'dwVipLv',  //VIP
		},
		{
			'name': 'dwVipPoint',   //VIP
		},
		{
			'name': 'dwClubPoint',  //
		},
        //
        {
			'name': 'dwCurExLevel',  //
		},
        {
			'name': 'dwNextExPoint',  //
		},
        {
			'name': 'dwIpAddress',  //
		},
		{
			'name': 'szNickName',  //
		},
		{
			'name': 'szSign',  //
		}
	],
	createFromStream: function (stream:any, args:any) {
		let dwGameID = stream.readUint32();
		let dwUserID = stream.readUint32();    
		let wFaceID = stream.readUint16();  
        let cbGender = stream.readUint8(); 
        let wTableID = stream.readUint16();
		let wChairID = stream.readUint16();    
		let cbUserStatus = stream.readUint8();  
        let lScore = stream.readUint64(); 
        let lDiamond = stream.readUint64();
		let lExperience = stream.readUint64();    
		let lLpValues = stream.readUint64();  
        let lTicket = stream.readUint64(); 
        let dwVipLv = stream.readUint32();
		let dwVipPoint = stream.readUint32();    
		let dwClubPoint = stream.readUint32();  
        let dwCurExLevel = stream.readUint32(); 
        let dwNextExPoint = stream.readUint32();
		let dwIpAddress = stream.readUint32(); 
		let szNickName = "";
		let szSign = "";
		//		
        for(let i = 0;i < 2;i++){
			let byteSize = stream.buffer.byteLength - stream.rpos;
            if(byteSize>0){
				let tmpSize = stream.readUint16();
				let tmpCmd = stream.readUint16();
				if(tmpCmd == DTP_GR_NICK_NAME){
					szNickName = stream.readString(tmpSize/2);
					if(szNickName === "") szNickName = "player";
				}
				else if(tmpCmd == DTP_GR_UNDER_WRITE){
					szSign = stream.readString(tmpSize/2);
					if(szSign === "") szSign = "empty";
				}
				else if(tmpCmd == 0) {
					break;
				}
				else{
					for(let i = 1;i<=tmpSize;i++){
						if(!stream.readUint8()){
							break;
						}
					}
				}
			}
			else {
				break;
			}
		}

		args.push(dwGameID, dwUserID, wFaceID,cbGender,wTableID,wChairID,cbUserStatus,lScore,lDiamond,lExperience,lLpValues,
            lTicket,dwVipLv,dwVipPoint,dwClubPoint,dwCurExLevel,dwNextExPoint,dwIpAddress,szNickName,szSign);
	}
};
