datatypes.CMD_S_GameScene = {
	mainCmdID: MDM_GF_FRAME,
	subCmdID: SUB_GF_GAME_SCENE,
	argTypes: [
		{
			name: 'lCellScore',    //
		},
		{
			name: 'cbBetCount',  //
		},
		{
			name: 'dBetScore',   //
		},
		{
			name: 'cbBetIndex',  //
		},
		{
			name: 'dLotteryScore',   //
		},
		{
			name: 'PoolNum',  //
		},
		{
			name: 'cbLineCount',     //
		},
        {
			name: 'dLineScore',   //  
		},
        {
			name: 'cbItemType',   //
		},
        {
			name: 'cbItemLineCount',    //
		},
        {
			name: 'cbItemInfo',   //
		},
        {
			name: 'nGiveAwayCount',   //
		},
        {
			name: 'nTotalGiveAwayCount',  //
		},
        {
			name: 'cbFreeSpinState',  //012,3
		},
        {
			name: 'dUserTotalWinScore',  //
		},
        {
			name: 'dTotalGameScore',  //
		},
        {
			name: 'nPoolLineCount',   //
		},
        {
			name: 'nPoolRewardScore',  //
		}
	],
	createFromStream: function (stream:any, args:any) {
		//let byteSize = stream.buffer.byteLength - stream.rpos;
            //if(byteSize>0){
		let lCellScore = stream.readUint64();
		let cbBetCount = stream.readUint8();
		let dBetScore = [];
		for(let i = 0;i<Constants.MAX_BET_COUNT;i++){
			dBetScore[i] = stream.readFloat();
		}

		let cbBetIndex = stream.readUint8();
		let dLotteryScore = stream.readUint64();
		let PoolNum = stream.readUint32();
		
		let cbLineCount = 0;
        let dLineScore = [];
		let cbItemType = [];
		let cbItemInfo = [];
		let cbItemLineCount = [];
		let nGiveAwayCount = 0;
		let nTotalGiveAwayCount = 0;
        let cbFreeSpinState = 0;
        let dUserTotalWinScore = 0;
		let dTotalGameScore = 0;
		let nPoolLineCount = 0;
		let nPoolRewardScore = 0;
		let byteSize = stream.buffer.byteLength - stream.rpos;
        if(byteSize>0){
			cbLineCount = stream.readUint8();
			for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
				dLineScore[i] = stream.readUint64();
			}
			for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
				cbItemType[i] = stream.readUint8();
			}
			for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
				cbItemLineCount[i] = stream.readUint8();
			}
			for(let i = 0;i<Constants.ITEM_Y_COUNT;i++){
				cbItemInfo[i] = [];
				for(let n = 0;n<Constants.ITEM_X_COUNT;n++){
					cbItemInfo[i][n] = stream.readUint8();
				}
			}
			nGiveAwayCount = stream.readUint32();
			nTotalGiveAwayCount = stream.readUint32();
			cbFreeSpinState = stream.readUint8();
			dUserTotalWinScore = stream.readUint64();
			dTotalGameScore = stream.readUint64();
			nPoolLineCount = stream.readUint32();
			nPoolRewardScore = stream.readUint32();
	    }
		args.push(lCellScore, cbBetCount, dBetScore, cbBetIndex, dLotteryScore, PoolNum,cbLineCount,dLineScore,cbItemType,cbItemLineCount,
            cbItemInfo,nGiveAwayCount,nTotalGiveAwayCount,cbFreeSpinState,dUserTotalWinScore,dTotalGameScore,nPoolLineCount,nPoolRewardScore
            );
	}
};
