import { _decorator, Button, Component, Node, SpriteFrame } from 'cc';
import { GameFacadeInstance } from '../GameFacade';
import { Constants } from '../Constants';
import { globalData } from '../GlobalData';
const { ccclass, property } = _decorator;

@ccclass('SetView')
export class SetView extends Component {
    @property(Button)
    btnMusic:Button = null;
    @property(Button)
    btnSound:Button = null;
    @property(SpriteFrame)
    btnChangeFrame:SpriteFrame[] = [];

    _musicEnable:boolean = true;
    _effectEnable:boolean = true;

    start() {
        this.initBtnState();
    }

    update(deltaTime: number) {
        
    }
    onBtnMusic(){
        this._musicEnable = !this._musicEnable;
        let frameIndex = this._musicEnable?0:1;
        this.btnMusic.normalSprite = this.btnChangeFrame[frameIndex];
    }
    onBtnSound(){
        this._effectEnable = !this._effectEnable;
        let frameIndex = this._effectEnable?0:1;
        this.btnSound.normalSprite = this.btnChangeFrame[frameIndex];
    }
    private initBtnState(){
        this._musicEnable = globalData.wMusicEnable;
        this._effectEnable = globalData.wEffctEnable;
        let frameIndex = this._musicEnable == true?0:1;
        this.btnMusic.normalSprite = this.btnChangeFrame[frameIndex];
        frameIndex = this._effectEnable == true?0:1;
        this.btnSound.normalSprite = this.btnChangeFrame[frameIndex];
    }
    onClose(){
        GameFacadeInstance.sendNotification(Constants.GMAENOTIFICATION_ID.SETAUDIOENABLE,this._musicEnable,this._effectEnable);
        GameFacadeInstance.sendNotification(Constants.GMAENOTIFICATION_ID.CLOSEPOPUPVIEW);
    }
}


