import {datatypes} from '../messageDef/GameProtocol'
import { Constants } from '../Constants';
const global = globalThis;
    
export let gameProxy = global.puremvc.define(
	{
		parent: global.puremvc.Proxy,
		constructor: function () {
			global.puremvc.Proxy.call(this, this.constructor.NAME);
		}
	},
	{
		onRegister: function () {
		
		},

		registerNetInterface: function (app:any) {
			this.app = app;
			let net = app.net;
			this.net = net;

			net.registerServer(this, 'onSubGameStart', datatypes.CMD_S_GameStart);	
            net.registerServer(this, 'onSubGameConclude', datatypes.CMD_S_GameConclude);	
            net.registerServer(this, 'onSubRewardPool', datatypes.CMD_S_RewardPool);	
            net.registerServer(this, 'onSubGameLottery', datatypes.CMD_S_Lottery);
            net.registerClient(this, 'sendGiveAwayStart', datatypes.CMD_C_GiveAwayStart);
            net.registerClient(this, 'sendGameStart', datatypes.CMD_C_GameStart);
            net.registerClient(this, 'sendGameConclude', datatypes.CMD_C_GameConclude);
		},
        //
        onSubGameStart:function(data:any){
            console.log('onSubGameStart');
            this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_GAMESTART,data);
        },
        onSubGameConclude:function(){
            console.log('onSubGameConclude');
            this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_GAMECONCLUDE);
        },
        onSubRewardPool:function(PoolNum:number){
            console.log('onSubRewardPool',PoolNum);
            this.sendNotification(Constants.GMAENOTIFICATION_ID.GAME_SENDREWARDPOOL,PoolNum);
        },
        onSubGameLottery:function(dLotteryScore:number){
            console.log('onSubGameLottery',dLotteryScore);
        },
        //
        requestSpin:function(betNum:number, isFree:boolean){
            if(!this.app.socket){
                return;
            }
            if(isFree) this.net.callServer('sendGiveAwayStart');
            else{
                this.net.callServer('sendGameStart',Constants.MAX_LINE_COUNT,betNum);
            }
        },
        requestGameConclude:function(){
            if(!this.app.socket){
                return;
            }
            this.net.callServer('sendGameConclude');
        }
	},
	{
		NAME: 'GameProxy'
	});