import {Constants} from "../Constants"
import {globalData} from "../GlobalData"
const global = globalThis;
export let GameLoginCommand  = global.puremvc.define(
	{
		parent: global.puremvc.SimpleCommand
	},
	{
		execute: function () {
            console.log("");
			let gameProxy = this.facade.retrieveProxy('GameProxy');
			let gameTableProxy = this.facade.retrieveProxy('GameTableProxy');
			let app = global.KBEngine.createApp();
			app.wssAddr = Constants.GAMESERVER_ADDR;
			gameProxy.registerNetInterface(app);
			gameTableProxy.registerNetInterface(app);
			// let wUserData = globalData.getUserData();
			// let userId = wUserData.userID;
			// let szToken = wUserData.szToken;
			//
			//gameTableProxy.sendPhoneNumLogin('13634120436');
			gameTableProxy.sendLogonRoomData();
		}
	},
	{
		NAME: 'GameLoginCommand'
	});