import { Constants } from "./Constants";
class GlobalData {
    wUserID:number = 0;
    wUserScore:number = 0;
    wSzNickName:string = 'unknown';
    wSzToken:string = '';
    wSzAccounts:string = '';
    wFaceID:number = 1;
    wTableID:number = Constants.INVALID_TABLE;  //id
    wChairID:number = Constants.INVALID_CHAIR;  //id
    wRoomUserList:{} = {};  //
    wTableUserList:{} = {}; //
    wChangeDesk:boolean = false;
    wGameStatus:number = Constants.GAME_STATUS.GAME_SCENE_FREE;
    wMusicEnable:boolean = true;
    wEffctEnable:boolean = true;
    static _classInstance:GlobalData = null; 
    
    constructor(){  

    }
    setUserAccountData(szToken:string,szAccounts:string){
        this.wSzToken = szToken;
        this.wSzAccounts = szAccounts;
    }
    setUserID(userID:number){
        this.wUserID = userID;
    }
    //
    onUpDataTableUser(tableid:number, chairid:number, useritem:any){
        let wTableid = tableid + 1;
        let wChairid = chairid + 1;
        
        if(!this.wTableUserList[tableid]){
            this.wTableUserList[tableid] = {};
        }
        if(useritem) {
            this.wTableUserList[tableid][chairid] = useritem.dwUserID;
        }
        else{
            delete this.wTableUserList[tableid][chairid];
        }
    }
    static getInstance(){
        if(!GlobalData._classInstance){
            GlobalData._classInstance = new GlobalData();
        }
        return GlobalData._classInstance;
    }
}
export let globalData = GlobalData.getInstance();