import { _decorator, Component, instantiate, Label, Node, Prefab, tween, Animation, Tween, find } from 'cc';
const { ccclass, property } = _decorator;
import { Constants } from '../Constants';
@ccclass('RewardAniNode')
export class RewardAniNode extends Component {
    @property(Prefab)
    normalRewardNode:Prefab = null;
    @property(Prefab)
    bigRewardNode:Prefab = null;
    @property(Prefab)
    poolRewardNode:Prefab = null;

    _poolReward:number = 0;
    _winScore:number = 0;
    _isBigReward:boolean = false;
    _gameScene:any = null;
    start() {
       
    }

    update(deltaTime: number) {
        
    }
    showRewardAni(poolReward:number,winScore:number,isBigReward:boolean,gameScene:any){ 
        if(this.node.active) return;
        this.node.active = true;
        this._poolReward = poolReward;
        this._winScore = winScore;
        this._isBigReward = isBigReward;
        this._gameScene = gameScene;
        if(poolReward>0){
            this.showPoolReward();
        }
        else{
            if(isBigReward) this.showBigReward();
            else this.showNormalReward();
        }
    }
    private showPoolReward(){
        let aniNode = instantiate(this.poolRewardNode);
        if(aniNode){
            this.node.addChild(aniNode);
            let labScore = aniNode.getChildByName('sprPool').getComponentInChildren(Label); 
            if(labScore){
                let addTimes = 20;
                let poolNum = 0;
                let addNum = this._poolReward/addTimes;
                let addNumTween  =  tween(labScore)
                                    .delay(0.05)
                                    .call(()=>{
                                        poolNum+=addNum;
                                        labScore.string = Constants.FormatScore(poolNum);
                                    });
                tween(labScore).repeat(addTimes,addNumTween).start();
            } 
            let poolAni = aniNode.getComponent(Animation);
            if(poolAni){
                poolAni.on(Animation.EventType.FINISHED,()=>{
                    if(this._isBigReward) this.showBigReward();
                    else if(this._winScore>0)this.showNormalReward();
                    else this.aniFinshCallBack();                  
                });
                poolAni.play();
            }
        }
    }
    private showBigReward(){
        let aniNode = instantiate(this.bigRewardNode);
        if(aniNode){
            this.node.addChild(aniNode);
            let labScore = aniNode.getChildByName('sprBigWin').getComponentInChildren(Label);
            if(labScore){
                let addTimes = 10;
                let curScore = 0;
                let addScore = this._winScore/addTimes;
                let addScoreTween = tween(labScore)
                                    .delay(0.05)
                                    .call(()=>{
                                        curScore+=addScore;
                                        labScore.string = Constants.FormatScore(curScore);
                                    });
                tween(labScore).repeat(addTimes,addScoreTween).start();
            }
            let bigRewardAni = aniNode.getComponent(Animation);
            if(bigRewardAni){
                bigRewardAni.on(Animation.EventType.FINISHED,this.aniFinshCallBack,this);
                bigRewardAni.play();
            }
        }
    }
    private showNormalReward(){
        let aniNode = instantiate(this.normalRewardNode);
        if(aniNode){
            this.node.addChild(aniNode);
            let labScore = find('aniNode/labScore',aniNode)?.getComponent(Label);
            if(labScore) labScore.string = Constants.FormatScore(this._winScore);
            let normalRewardAni = aniNode.getComponent(Animation);
            if(normalRewardAni){
                normalRewardAni.on(Animation.EventType.FINISHED,this.aniFinshCallBack,this);
                normalRewardAni.play();
            }
        }
    }
    private aniFinshCallBack(){
        this.node.active = false;
        this.node.destroyAllChildren();
        this._gameScene.rewardFinish();
    }
    protected onDisable(): void {
        Tween.stopAll();
    }
}


