import { _decorator, Component, Node, CCInteger, resources, randomRange, SpriteAtlas, Sprite, UITransform, SpriteFrame, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;
import { Constants } from '../Constants';
import { GameFacadeInstance } from '../GameFacade';
const BOTTOM_HEIGHT = -140;
const ITEM_HEIGHT = 140;
@ccclass('FruitRollControl')
export class FruitRollControl extends Component {
    @property(Node)
    rollLayout:Node = null;
    @property(CCInteger)
    moveSpeed = 60;
    @property(CCInteger)
    speedDown = 5;
    @property(CCInteger)
    minSpeed = 50;
    @property(SpriteAtlas)
    fruitSpriteAtlas:SpriteAtlas = null;
    
    m_fruitItemCount:number = 8;
    m_readyEnd:boolean = false;
    m_rollStatus:number = Constants.ROLL_STATUS.Stop_Status;
    m_rollSpeed:number = this.moveSpeed;
    m_checkIndex:number = 0;
    m_resultData:number[] = [];
    start() {
        this.initFruit();
    }

    update(deltaTime: number) {
        
    }
    onDestroy() {
        this.unschedule(this.updateRoll);
    }
    //
    initFruit(){
        if(this.rollLayout.children.length>0){
            this.rollLayout.destroyAllChildren();
        }
        this.m_readyEnd = false;       
        this.m_checkIndex = 0;
        this.m_rollSpeed = this.moveSpeed;
        for(let i = 0;i<this.m_fruitItemCount;i++){
            let fruitName = 'im_fruit_'+Math.floor(randomRange(0,11));
            let frame = this.fruitSpriteAtlas.getSpriteFrame(fruitName);
            if(frame){
                let fruitNode = new Node('fruit_'+i);
                let fruitSpr = fruitNode.addComponent(Sprite);
                fruitSpr.spriteFrame = frame;
                fruitNode.setScale(0.8,0.8);
                this.rollLayout.addChild(fruitNode);
                fruitNode.setPosition(0,BOTTOM_HEIGHT+ITEM_HEIGHT*i);
            }
        }
    }
    updateRoll(){
        if(this.m_rollStatus == Constants.ROLL_STATUS.Stop_Status) return;
        if(this.m_rollStatus == Constants.ROLL_STATUS.SlowDown_Status){
            if(this.m_rollSpeed>this.minSpeed){
                this.m_rollSpeed-=this.speedDown;
                if(this.m_rollSpeed<this.minSpeed) this.m_rollSpeed = this.minSpeed;
            }
        }
        for(let fruitNode of this.rollLayout.children){
            let nodePos = fruitNode.getPosition();
            nodePos.y-=this.m_rollSpeed;
            fruitNode.setPosition(nodePos);
        }   
        let checkFruitNode = this.rollLayout.getChildByName('fruit_'+this.m_checkIndex);
        if(!checkFruitNode) return;
        let nodePos = checkFruitNode.getPosition();
        if(nodePos.y<=(BOTTOM_HEIGHT-ITEM_HEIGHT)){
            nodePos.y = nodePos.y+ITEM_HEIGHT*this.m_fruitItemCount;
            checkFruitNode.setPosition(nodePos);           
            let fruitName = 'im_fruit_'+Math.floor(randomRange(0,11));                   
            let fruitFrame = this.fruitSpriteAtlas.getSpriteFrame(fruitName);
            if(fruitFrame) checkFruitNode.getComponent(Sprite).spriteFrame = fruitFrame; 
            this.m_checkIndex+=1;
            if(this.m_checkIndex>=this.m_fruitItemCount) this.m_checkIndex = 0;         
            if(this.m_rollSpeed == this.minSpeed){                          
                if(this.m_checkIndex == 0){
                    this.preLoadResult();
                }
                if(this.m_readyEnd&&this.m_checkIndex==(this.m_fruitItemCount-3)){
                    this.setRollStatus(Constants.ROLL_STATUS.Stop_Status);
                }
            }
        }
    }
    //
    setRollStatus(rollStatus:number){
        if(rollStatus<Constants.ROLL_STATUS.RollNormall_Status || rollStatus>Constants.ROLL_STATUS.Stop_Status) return;
        if(this.m_rollStatus == rollStatus) return;
        this.m_rollStatus = rollStatus;
        //
        if(this.m_rollStatus == Constants.ROLL_STATUS.Stop_Status){
            this.unschedule(this.updateRoll);
            // 3
            let posIndex = 0;
            for(let i = this.m_checkIndex;i<this.m_fruitItemCount;i++){
                let fruitNode = this.rollLayout.getChildByName('fruit_'+i);
                if(fruitNode){
                    fruitNode.setPosition(0,BOTTOM_HEIGHT+ITEM_HEIGHT*(posIndex++));
                    tween(fruitNode)
                         .by(0.2,{position:new Vec3(0,-5,0)})
                         .by(0.2,{position:new Vec3(0,10,0)})
                         .by(0.2,{position:new Vec3(0,-5,0)})
                         .start();
                }
            }
            this.rollLayout.getChildByName('fruit_0').setPosition(0,BOTTOM_HEIGHT+ITEM_HEIGHT*posIndex);
            //   
            GameFacadeInstance.sendNotification(Constants.GMAENOTIFICATION_ID.FRUITROLLFINISH);
        }
        //
        else if(rollStatus == Constants.ROLL_STATUS.RollNormall_Status){
            this.initFruit();
            this.schedule(this.updateRoll,0.02);
        }
    }
    //
    preLoadResult(){
        for(let i = 0;i<this.m_resultData.length;i++){
            let fruitFrame = this.fruitSpriteAtlas.getSpriteFrame('im_fruit_'+this.m_resultData[i]);
            if(fruitFrame){
                let fruitNode = this.rollLayout.getChildByName('fruit_'+(this.m_fruitItemCount-1-i));
                if(fruitNode){
                    fruitNode.getComponent(Sprite).spriteFrame = fruitFrame;
                } 
            }
        }
        this.m_readyEnd = true;
    }
    //
    setResultData(data1:number,data2:number,data3:number){
        this.m_resultData[0] = data1;
        this.m_resultData[1] = data2;
        this.m_resultData[2] = data3;
    }
    //
    setShowState(isGray:boolean){
        for(let fruitNode of this.rollLayout.children){
            let frameName = fruitNode.getComponent(Sprite).spriteFrame.name;
            let newFrame = null;
            let newFrameName = null;
            if(isGray) newFrameName = 'im_fruit_gray_'+frameName.substring(frameName.length-1);           
            else newFrameName = 'im_fruit_'+frameName.substring(frameName.length-1); 
            if(newFrameName &&newFrameName != frameName) newFrame = this.fruitSpriteAtlas.getSpriteFrame(newFrameName);
            if(newFrame) fruitNode.getComponent(Sprite).spriteFrame = newFrame;
        }
    }
    //
    setRewardItem(childIndex:number){
        console.log('',childIndex);
        let fruitSprite = this.rollLayout.getChildByName('fruit_'+(childIndex+4))?.getComponent(Sprite);
        if(fruitSprite){
            let frameName = fruitSprite.spriteFrame.name;
            let newFrameName = 'im_fruit_'+frameName.substring(frameName.length-1);
            if(frameName!=newFrameName){
                let newFrame = this.fruitSpriteAtlas.getSpriteFrame(newFrameName);
                if(newFrame) fruitSprite.spriteFrame = newFrame;
            }  
        }
    }
}


