import {Constants} from "../Constants"
export let datatypes = {} as any;
//
const MDM_GF_GAME = 1200;
//
const SUB_S_GAME_START	      =100;   //
const SUB_S_GAME_CONCLUDE     =101;   //
const SUB_S_GAME_LOTTERY      =102;  //
const SUB_S_SEND_REWARD_POOL  =107;   //

//
datatypes.CMD_S_GameStart = {
	mainCmdID: MDM_GF_GAME,
	subCmdID: SUB_S_GAME_START,
	argTypes: [
		{
			name: 'cbItemInfo', //
		},
		{
			name: 'dLineScore',   //
		},
		{
			name: 'cbItemType',  //
		},
		{
			name: 'cbItemLineCount',   //
		},
		{
			name: 'nGiveAwayCount',   //
		},
		{
			name: 'nTotalGiveAwayCount',   //
		},
		{
			name: 'cbFreeSpinState',   //012,3
		},
		{
			name: 'dUserTotalWinScore',   //
		},
		{
			name: 'nPoolLineCount',   //
		},
		{
			name: 'nPoolRewardScore',   //
		}
	],
	createFromStream: function (stream:any, args:any) {
		let cbItemInfo = [];
		for(let i= 0;i<Constants.ITEM_Y_COUNT;i++){
			cbItemInfo[i] = [];
			for(let n = 0;n<Constants.ITEM_X_COUNT;n++){
				let itemInfo = stream.readUint8();
				cbItemInfo[i].push(itemInfo);
			}
		}
		let dLineScore = [];
		for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
			let lineScore = stream.readUint64();
			dLineScore.push(lineScore);
		}
		let cbItemType = [];
		for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
			let itemType = stream.readUint8();
			cbItemType.push(itemType);
		}
		let cbItemLineCount = [];
		for(let i = 0;i<Constants.MAX_LINE_COUNT;i++){
			let lineCount = stream.readUint8();
			cbItemLineCount.push(lineCount);
		}
		let nGiveAwayCount = stream.readUint32();
		let nTotalGiveAwayCount = stream.readUint32();
		let cbFreeSpinState = stream.readUint8();
		let dUserTotalWinScore = stream.readUint64();
		let nPoolLineCount = stream.readUint32();
		let nPoolRewardScore = stream.readUint32();
		args.push(cbItemInfo, dLineScore, cbItemType, cbItemLineCount,nGiveAwayCount,nTotalGiveAwayCount,cbFreeSpinState,dUserTotalWinScore,nPoolLineCount,nPoolRewardScore);
	}
};
