import{gameTableProxy} from "./model/GameTableProxy"
import { gameProxy } from "./model/GameProxy";
import{GameLoginCommand} from"./controller/GameLoginCommand"
import{Constants} from"./Constants"
const global = globalThis;

let GameFacade = global.puremvc.define(
    {
        parent: global.puremvc.Facade,
        constructor: function (multitonKey:any) {
            global.puremvc.Facade.call(this, multitonKey);
        }
    },
    {
        initializeModel: function () {
            global.puremvc.Facade.prototype.initializeModel.call(this);
        },

        initializeView: function () {
            global.puremvc.Facade.prototype.initializeView.call(this);
        },

        initializeController: function () {
            global.puremvc.Facade.prototype.initializeController.call(this);
        },

        startup: function () {               
            this.registerCommand(Constants.GMAENOTIFICATION_ID.GAME_LOGIN, GameLoginCommand);
            this.addProxy(new gameTableProxy());
            this.addProxy(new gameProxy());
            this.registerProxy();        
        }
    },
    {
        getInstance: function (multitonKey:any) {
            let instanceMap = global.puremvc.Facade.instanceMap;
            let instance = instanceMap[multitonKey];
            if (instance) {
                return instance;
            } else {
                instance = instanceMap[multitonKey] = new GameFacade(multitonKey);
                instance.startup();
                return instance;
            }
        },

        NAME: 'GameFacade'
    });
export let GameFacadeInstance = GameFacade.getInstance('GameFacade');
